import React from "react";
import {
    Switch,
    Route,
} from "react-router-dom";
import {Navbar, Container, Nav} from "react-bootstrap";
import {Home} from "./home";
import {Scoreboard} from "./scoreboard";
import {PlayerStats} from "./player-stats";
import 'bootstrap/dist/css/bootstrap.css';
import "toastr/toastr.scss";
import {RecordGame} from "./record-game";

export class NavBar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {environment : null};
    }

    getHost() {
        if (process.env.NODE_ENV !== 'development') {
            return "https://harrisoncaughey.github.io/KlaskScoreboard"
        } else {
            return "http://localhost:3000/KlaskScoreboard"
        }
    }

    render() {
        console.log(process.env)
        return (
            <div>
                <Navbar bg="dark" variant="dark" expand="lg">
                    <Container>
                        <Nav className="me-auto">
                            <Nav.Link href={`${this.getHost()}/`}>Home</Nav.Link>
                            <Nav.Link href={`${this.getHost()}/record`}>Record Results</Nav.Link>
                            <Nav.Link href={`${this.getHost()}/scoreboard`}>Scoreboard</Nav.Link>
                            <Nav.Link href={`${this.getHost()}/stats`}>Player Stats</Nav.Link>
                        </Nav>
                    </Container>
                </Navbar>
                <Switch>
                    <Route exact path="/">
                        <Home />
                    </Route>
                    <Route path="/record">
                        <RecordGame />
                    </Route>
                    <Route path="/scoreboard">
                        <Scoreboard />
                    </Route>
                    <Route path="/stats">
                        <PlayerStats />
                    </Route>
                </Switch>
            </div>
        )
    }
}

